.rate{
  &__wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    font-weight: bold;
    font-size: 30px;
    color: #232B3F;

    text-align: center;
  }
  &__img{
    margin-top: 20px;

    width: 185px;
    height: 185px;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__content{
    margin-top: 20px;
    &-title{
      font-weight: 500;
      font-size: 18px;
      color: #232B3F;

      text-align: center;
    }
    &-stars{
      margin-top: 15px;
    }
  }
  &__btn{
    margin-top: 35px;

    max-width: 380px;
    height: 45px;
    width: 100%;


    font-weight: bold;
    font-size: 18px;

    color: #FFFFFF;

    background: #E5134A;
    border-radius: 4px;

    display: flex;
    align-items: center;
    justify-content: center;

    text-align: center;
  }
}

.stars-block{
  display: flex;
  align-items: center;
  justify-content: center;
  &__item{
    width: 64px;
    height: 64px;

    display: flex;
    align-items: center;
    justify-content: center;
    &:hover{
      cursor: pointer;
    }
    &.filled{
      svg{
        fill: #FFCE31;
      }
    }
    svg{
      width: 32px;
      height: 32px;
    }
  }
}