.program{
  &-grid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    gap: 30px;

    grid-auto-rows: 580px;
  }
  &-item{
    height: 100%;
    &__wrapper{
      background: #EBEFF6;
      border-radius: 15px;

      overflow: hidden;

      height: 100%;

      display: flex;
      flex-direction: column;
    }
    &__header{
      position: relative;

      height: 250px;
    }

    &__img{
      width: 100%;
      height: 100%;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }

    &__category{
      position: absolute;
      top: 20px;
      right: 20px;

      display: flex;
      align-items: center;
      justify-content: center;

      padding: 6px 9px;

      border-radius: 4px;
      &_new{
        background: #232B3F;
      }
      &_hit{
        background-color: $accentColor;
        text-transform: uppercase;
      }
      &-text{
        color: #FFFFFF;
        font-size: 18px;
        font-weight: 700;
      }
    }

    &__rating{
      position: absolute;
      right: 20px;
      bottom: 20px;

      display: flex;
      align-items: center;
      &-icon{
        width: 20px;
        height: 20px;

        svg{
          width: 100%;
          height: 100%;
        }
      }
      &-number{
        margin-left: 5px;

        font-weight: 700;
        font-size: 20px;
        color: #232B3F;
      }
    }
    &__content{
      margin-top: 10px;

      padding: 20px 20px 0 20px;
    }

    &__title{
      font-weight: 700;
      font-size: 25px;
      line-height: 110%;
      letter-spacing: -0.02em;
      color: #232B3F;
    }
    &__desc{
      margin-top: 10px;

      font-weight: 500;
      font-size: 18px;
      line-height: 136%;
      color: #232B3F;
    }

    &__footer{
      margin-top: auto;
      padding: 0 20px 20px 20px;

      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__btn{
      display: flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;

      width: 130px;
      height: 45px;

      background: $accentColor;
      border-radius: 4px;

      font-weight: 700;
      font-size: 18px;
      color: #FFFFFF;
    }
    &__time{
      display: flex;
      align-items: center;
      &-icon{
        width: 24px;
        height: 24px;

        svg{
          width: 100%;
          height: 100%;
        }
      }
      &-text{
        margin-left: 8px;

        font-weight: 600;
        font-size: 18px;
        color: #8FA1C2;
      }
    }
  }
  &-tabs{
    &__header{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__nav{
      display: flex;
      align-items: center;
      &-item + &-item{
        margin-left: 15px;
      }
      &-item{
        &.active{
          .program-tabs__nav-link{
            color:#232B3F;

            background-color: #CCD7EA;
            border-color: transparent;
          }
        }
      }
      &-link{
        display: flex;
        align-items: center;
        justify-content: center;

        padding: 10px;

        height: 40px;

        min-width: 150px;

        font-weight: 600;
        font-size: 16px;

        color: #8A8A95;

        border: 1px solid #F1EEEA;
        border-radius: 20px;

        background-color: transparent;

        transition: .3s all;
        &__count{
          display: flex;
          align-items: center;
          justify-content: center;

          margin-left: 10px;

          width: 28px;
          height: 28px;

          border-radius: 50%;
          background: #FFFFFF;

          font-weight: bold;
          font-size: 16px;
          color: #232B3F;
        }
      }
    }
  }
  &-empty{
    margin: 80px auto 0 auto;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    max-width: 370px;

    &__block + &__block{
      margin-top: 35px;
    }
    &__text{
      font-weight: 500;
      font-size: 18px;
      text-align: center;

      color: #232B3F;
      &-accent{
        color:$accentColor;
      }
    }
    &__btn{
      display: flex;
      align-items: center;
      justify-content: center;

      width: 150px;
      height: 40px;

      font-weight: bold;
      font-size: 18px;
      text-align: center;

      color: #FFFFFF;

      background: #E5134A;
      border-radius: 4px;
    }
  }
}

@media screen and (max-width: 1023px) {
  .program{
    &-grid{
      grid-template-columns: repeat(2,minmax(0,1fr));
    }
  }
}

@media screen and (max-width: 767px) {
  .program{
    &-grid{
      grid-template-columns: minmax(0,1fr);
    }
    &-tabs{
      &__header{
        flex-direction: column;
        align-items: flex-start;
      }
      &__nav{
        margin-top: 30px;
      }
    }
  }
}