.course{
  &__container{
    background: #EBEFF6;

    padding:40px 0;
    position: relative;
    &-img{
      position: absolute;

      right: 0;
      top: 0;

      height: 100%;
      width: 30%;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &__wrapper{
    padding-top: 20px;

    max-width: 670px;
    width: 100%;
  }
  &__title{
    font-weight: bold;
    font-size: 30px;
    line-height: 37px;
    color: #071013;
  }
  &__desc{
    margin-top: 15px;

    font-weight: 500;
    font-size: 18px;
    line-height: 136%;

    color: #232B3F;

    max-width: 600px;
    width: 100%;
  }
  &__img{
    display: none;
  }
  &__duration{
    margin-top: 40px;
  }
  &__btn{
    margin-top: 40px;

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 10px 30px;

    width: max-content;

    font-weight: 600;
    font-size: 18px;

    text-align: center;

    color: #FFFFFF;
    &_disabled{
      background: #D3D3D3;
      color:#8A8A95;

      pointer-events: none;
    }
  }
  &__footer{
    margin-top: 15px;
    &-text{
      font-weight: 500;
      font-size: 18px;
      line-height: 136%;
      color: #232B3F;
    }
  }
  &-duration{
    &__wrapper{
      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      background: #CCD7EA;
      backdrop-filter: blur(25px);

      max-width: 370px;
      width: 100%;

      padding: 22px 28px;

      border-radius: 15px;
    }
    &__title{
      font-weight: bold;
      font-size: 23px;
      line-height: 28px;
      color: #232B3F;
    }
    &__desc{
      margin-top: 5px;

      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: #232B3F;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    &-item + &-item{
      margin-left: 40px;
    }
    &-item{
      &.active{
        .course__nav-link{
          color:#232B3F;

          border-bottom: 2px solid $accentColor;
        }
      }
    }
    &-link{
      font-weight: bold;
      font-size: 23px;
      line-height: 28px;

      color: #8A8A95;
    }
  }
  &__info{
    padding-top: 40px;
  }
  &__content{
    display: grid;
    grid-template-columns: minmax(0,1fr);
    gap: 20px;

    max-width: 670px;
    width: 100%;
    h3{
      font-weight: 700;
      color:#232B3F;
      font-size: 20px;
    }
    p{
      font-weight: 500;
      font-size: 18px;
      color:#232B3F;
    }
  }
  &__progress{
    margin-top: 15px;
  }
  &-progress{
    &__text{
      font-weight: 600;
      font-size: 18px;
      color: #232B3F;

      margin-bottom: 10px;
      &-dark{
        color: rgba(#232B3F,.5);
      }
    }
    &__bar{
      margin-top: 15px;
      width: 100%;
      height: 4px;

      background: rgba(#071013,.1);
      border-radius: 2px;
      position: relative;
      &-line{
        width: 10%;

        position: absolute;
        top: 0;
        left: 0;

        height: 100%;
        background: #E5134A;
        border-radius: 2px;
      }
    }
  }
  &-tabs{
    &__content{
      padding-top: 40px;
    }
  }
  &-lessons{
    &__wrapper{
      display: grid;
      grid-template-columns: minmax(0,1fr);
      gap: 15px;
    }
  }
  &-lesson{
    background: #EBEFF6;
    border-radius: 25px;
    &_disabled{
      opacity: .5;

      pointer-events: none;
      .course{
        &-lesson{
          &__status,&__header::after{
            display: none;
          }
        }
      }
    }
    &__header{
      display: flex;
      align-items: center;

      height: 80px;
      padding: 20px;
      &::after{
        content: '';
        width: 8px;
        height: 12px;
        display: block;
        margin-left: 25px;

        background: url("../img/arrow-course.svg") center center/contain no-repeat;

        transition: 0.3s all;
      }
      &.active{
        &::after{
          transform: rotate(90deg);
        }
      }
    }
    &__title{
      width: 115px;

      font-weight: 600;
      font-size: 18px;

      color: rgba(#232B3F,.5);
    }
    &__text{
      font-weight: 500;
      font-size: 18px;

      color: #232B3F;
      margin-left: 15px;
    }
    &__wrapper{
      display: none;

      padding: 0 20px 20px 20px;
      &.active{
        display: block;
      }
    }
    &__content{
      display: flex;
      align-items: flex-start;
    }
    &__desc{
      max-width: 620px;
      width: 100%;
    }
    &__status{
      margin-left: auto;
      padding: 8px 16px;

      background: #CCD7EA;
      border-radius: 21px;

      font-weight: 600;
      font-size: 18px;
      color: #232B3F;
      &_new{
        color:#E5134A;

        background: rgba(229, 19, 74, 0.1);

        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.1);
      }
    }
    &__footer{
      margin: 30px 0 0 0;

      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
    &__btn{
      width: 255px;
      height: 42px;

      display: flex;
      align-items: center;
      justify-content: center;

      font-size: 18px;
      font-weight: 600;
    }
  }
}

@media screen and (max-width: 1140px) {
  .course{
    &__container{
      &-img{
        width: 50%;
      }
    }
    &__wrapper{
      max-width: 50%;

      padding-right: 30px;
    }
  }
}

@media screen and (max-width: 767px) {
  .course{
    &__container{
      &-img{
        display: none;
      }
    }
    &__wrapper{
      max-width: 100%;
      padding-right: 0;
    }
    &__img{
      display: block;
      margin-top: 15px;

      width: 100%;
      height: 430px;
      &-item{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-lesson{
      position: relative;
      &.active{
        .course{
          &-lesson{
            &__status{
              position: absolute;
              right: 20px;
              bottom: 20px;
            }
          }
        }
      }
      &__header{
        flex-direction: column;
        height: auto;
        align-items: flex-start;
        &::after{
          position: absolute;
          top: 28px;
          right: 28px;
        }
      }
      &__text{
        margin-left: 0;
        margin-top: 15px;
      }
      &__status{
        margin: 10px 0 0 auto;
      }
      &__footer{
        justify-content: flex-start;
      }
      &__btn{
        width: 50%;
        font-size: 16px;
      }
      &__content{
        flex-direction: column;
      }
    }
  }
}