.section-certificate{
  &__wrapper{
    display: grid;
    grid-template-columns: 270px auto;

    gap: 130px;

    padding-top: 20px;
  }
}

.certificate{
  &-share{
    margin-top: 20px;
    &__social{
      width: 270px;
    }
    &__title{
      text-align: center;
      font-weight: 500;
      font-size: 18px;

      color:rgba(#071013,.5);

      margin-bottom: 20px;
    }
  }
  &-info{
    &__title{
      font-weight: bold;
      font-size: 30px;
      line-height: 37px;
      color: #071013;
    }
    &__name{
      margin-top: 15px;

      font-weight: bold;
      font-size: 23px;
      line-height: 28px;

      color: #E5134A;
    }
    &__date{
      margin-top: 20px;

      font-weight: 500;
      font-size: 18px;

      color:rgba(#071013,.5)
    }
    &__download{
      width: 100%;
      height: 42px;

      display: flex;
      align-items: center;
      justify-content: center;

      font-weight: 600;
      font-size: 18px;
      color: #FFFFFF;

      background: #E5134A;
      border-radius: 4px;

      margin-top: 40px;
    }
    &__btn{
      margin-top: 40px;

      width: 100%;
      height: 42px;

      display: flex;
      align-items: center;
      justify-content: center;

      background: #CCD7EA;
      border-radius: 4px;

      font-weight: 600;
      font-size: 18px;
      color: #232B3F;
    }
  }
  &__wrapper{
    height: 1110px;
    padding: 70px;

    background-color: #232B3F;

    position: relative;

    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;

      width: 100%;
      height: 100%;
      display: block;

      background: url("../img/certificate-bg.svg") center center/cover no-repeat;
    }
  }
  &__logo{
    width: 200px;
    height: 90px;

    &-item{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__title{
    margin-top: 130px;

    font-family: 'Coco Gothic', sans-serif;
    font-weight: bold;
    font-size: 62px;
    line-height: 84px;

    color: #FFFFFF;
  }
  &__content{
    position: relative;
    z-index: 2;

    display: flex;
    flex-direction: column;
    height: 100%;
  }
  &__text{
    font-size: 22px;
    line-height: 26px;
    text-transform: uppercase;

    color: #FFFFFF;

    margin-top: 55px;
  }
  &__name{
    margin-top: 20px;

    font-family: 'Coco Gothic', sans-serif;
    font-weight: bold;
    font-size: 34px;
    line-height: 46px;
    text-transform: uppercase;

    color:#fff;
  }
  &__end{
    margin-top: 150px;

    font-size: 22px;
    line-height: 26px;

    color: #FFFFFF;
    &-bold{
      font-weight: 700;
    }
  }
  &__date{
    margin-top: auto;

    font-weight: bold;
    font-size: 22px;
    line-height: 27px;

    color: #FFFFFF;
    &-number{
      font-weight: 400;
    }
  }
}

@media screen and (max-width: 1140px) {
  .section-certificate{
    &__wrapper{
      gap: 30px;
    }
  }
  .certificate{
    &__wrapper{
      padding: 45px;
      height: 664px;
    }
    &__logo{
      width: 120px;
      height: 55px;
    }
    &__title{
      margin-top: 80px;

      font-size: 38px;
      line-height: 52px;
    }
    &__text{
      margin-top: 32px;

      font-size: 13px;
      line-height: 16px;
    }
    &__name{
      margin-top: 12px;

      font-size: 21px;
      line-height: 28px;
    }
    &__end{
      margin-top: 100px;

      font-size: 13px;
      line-height: 16px;
    }
    &__date{
      font-size: 13px;
      line-height: 16px;
    }
  }
}

@media screen and (max-width: 767px) {
  .section-certificate__wrapper{
    grid-template-columns: minmax(0,1fr);
    gap: 60px;
  }
  .certificate{
    &__wrapper{
      padding: 35px;
      height: 525px;
    }
    &__logo{
      width: 95px;
      height: 45px;
    }
    &__title{
      font-size: 30px;
      line-height: 41px;
    }
    &__text{
      margin-top: 25px;

      font-size: 10px;
      line-height: 13px;
    }
    &__name{
      margin-top: 10px;

      font-size: 16px;
      line-height: 22px;
    }
    &__end{
      margin-top: 80px;

      font-size: 10px;
      line-height: 13px;
    }
    &__date{
      font-size: 10px;
      line-height: 13px;
    }
  }
}