.article{
  &__title{
    font-weight: bold;
    font-size: 30px;
    line-height: 37px;
    color: #071013;
  }
  &__category{
    margin-top: 15px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__wrapper{
    max-width: 670px;
    width: 100%;
  }
  &__content{
    display: grid;
    grid-template-columns: minmax(0,1fr);
    gap: 20px;

    margin-top: 15px;
    img{
      width: 100%;
      height: 333px;

      object-fit: cover;
      border-radius: 15px;
    }
    h3{
      font-weight: 700;
      color:#232B3F;
      font-size: 20px;
    }
    p{
      font-weight: 500;
      font-size: 18px;
      color:#232B3F;
    }
  }
  &-category{
    &__item{
      margin: 5px;
    }
    &__label{
      font-weight: 600;
      font-size: 18px;
      color:#232B3F;

      display: flex;
      align-items: center;
      width: max-content;

      padding: 5px 10px;

      background: rgba(35, 43, 63, 0.1);
      border-radius: 15px;
    }
  }
}